import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useUser } from '../context/UserContext';
import { neighborhoodAPI } from '../services/api';
import { Heart, MapPin, Star, TrendingUp, Navigation, Briefcase, Users, Sparkles, Building2 } from 'lucide-react';
import { useToast } from '../context/ToastContext';
import { userAPI } from '../services/api';
import AIRecommendationEngine from '../components/ai/AIRecommendationEngine';

function Dashboard() {
  const { user, preferences, setUser } = useUser();
  const [neighborhoods, setNeighborhoods] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const { showSuccess, showError } = useToast();

  const fetchNeighborhoods = useCallback(async () => {
    try {
      setLoading(true);
      const response = await neighborhoodAPI.getAll();
      // Get top 3 neighborhoods based on various scores
      const sortedNeighborhoods = response.data
        .sort((a, b) => {
          const scoreA = (a.safetyScore + a.lifestyleScore + a.transportScore) / 3;
          const scoreB = (b.safetyScore + b.lifestyleScore + b.transportScore) / 3;
          return scoreB - scoreA;
        })
        .slice(0, 3)
        .map((neighborhood, index) => ({
          ...neighborhood,
          matchScore: Math.round(92 - (index * 5)) // Simulated match scores
        }));
      setNeighborhoods(sortedNeighborhoods);
    } catch (error) {
      console.error('Error fetching neighborhoods:', error);
      showError('Failed to fetch neighborhoods. Please try again.');
    } finally {
      setLoading(false);
    }
  }, [showError]);

  useEffect(() => {
    if (user) {
      fetchNeighborhoods();
    }
  }, [user, fetchNeighborhoods]);

  const handleViewDetails = (neighborhoodId) => {
    navigate(`/neighborhoods/${neighborhoodId}`);
  };

  const handleToggleFavorite = async (neighborhoodId) => {
    try {
      // Check if user is logged in
      if (!user) {
        showError('Please log in to save favorites.');
        return;
      }

      // Check if neighborhood is already in favorites
      const isCurrentlyFavorite = user.favoriteNeighborhoods?.includes(neighborhoodId);
      
      let response;
      if (isCurrentlyFavorite) {
        // Remove from favorites
        response = await userAPI.removeFavorite(neighborhoodId);
        showSuccess('Neighborhood removed from favorites!');
      } else {
        // Add to favorites
        response = await userAPI.addFavorite(neighborhoodId);
        showSuccess('Neighborhood added to favorites!');
      }
      
      // Update user context with the response from the API
      if (response && response.data) {
        setUser({
          ...user,
          favoriteNeighborhoods: response.data.favoriteNeighborhoods || []
        });
      }
    } catch (error) {
      showError('Failed to update favorites. Please try again.');
    }
  };

  const handleGetDirections = (neighborhood) => {
    if (!neighborhood || !neighborhood.location || !neighborhood.location.coordinates) {
      showError('Location coordinates not available for this neighborhood.');
      return;
    }

    const [longitude, latitude] = neighborhood.location.coordinates;
    const destination = `${latitude},${longitude}`;

    // Check if user is on mobile device
    const isMobile = /Android|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

    let mapUrl;
    
    if (isMobile) {
      // For mobile devices, try to open native apps first
      if (/iPhone|iPad|iPod/i.test(navigator.userAgent)) {
        // iOS - try Apple Maps first, fallback to Google Maps
        mapUrl = `maps://maps.apple.com/?daddr=${destination}&dirflg=d&t=m`;
        
        // Try to open Apple Maps
        const appleMapsLink = document.createElement('a');
        appleMapsLink.href = mapUrl;
        appleMapsLink.click();
        
        // Fallback to Google Maps after a short delay
        setTimeout(() => {
          window.open(`https://maps.google.com/maps?daddr=${destination}&dirflg=d`, '_blank');
        }, 500);
        return;
      } else if (/Android/i.test(navigator.userAgent)) {
        // Android - try Google Maps app first
        mapUrl = `google.navigation:q=${destination}`;
        
        // Try to open Google Maps app
        const googleMapsLink = document.createElement('a');
        googleMapsLink.href = mapUrl;
        googleMapsLink.click();
        
        // Fallback to web version
        setTimeout(() => {
          window.open(`https://maps.google.com/maps?daddr=${destination}&dirflg=d`, '_blank');
        }, 500);
        return;
      }
    }

    // For desktop or fallback, open Google Maps in web browser
    mapUrl = `https://maps.google.com/maps?daddr=${destination}&dirflg=d`;
    window.open(mapUrl, '_blank');
    
    showSuccess('Opening directions in map application...');
  };

  if (!user) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900 mb-4">Welcome to NeighborFit!</h2>
          <p className="text-gray-600 mb-6">Complete your onboarding to see your matches</p>
          <button 
            onClick={() => navigate('/onboarding')} 
            className="bg-gradient-to-r from-blue-500 to-purple-600 text-white font-semibold py-3 px-8 rounded-full hover:from-blue-600 hover:to-purple-700 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105"
          >
            Get Started
          </button>
        </div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading your matches...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-purple-50 to-pink-100 py-8 flex flex-col">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 w-full">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-extrabold text-gradient bg-gradient-to-r from-indigo-700 via-purple-600 to-pink-500 bg-clip-text text-transparent drop-shadow-lg">
            Welcome back, {user.name}! <span className="inline-block animate-waving-hand"></span>
          </h1>
          <p className="text-lg text-gray-700 mt-2 font-medium">
            Here are your personalized neighborhood matches in <span className="font-semibold text-indigo-600">{preferences?.city || 'India'}</span>
          </p>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <div className="card glassmorphism text-center shadow-glow card-hover transition-all duration-300">
            <div className="w-12 h-12 bg-gradient-to-br from-indigo-200 to-indigo-100 rounded-xl flex items-center justify-center mx-auto mb-3 shadow-md">
              <TrendingUp className="w-6 h-6 text-indigo-600" />
            </div>
            <h3 className="text-3xl font-extrabold text-gray-900 tracking-tight">{neighborhoods.length}</h3>
            <p className="text-gray-600 font-medium">Total Matches</p>
          </div>
          <div className="card glassmorphism text-center shadow-glow card-hover transition-all duration-300">
            <div className="w-12 h-12 bg-gradient-to-br from-pink-200 to-pink-100 rounded-xl flex items-center justify-center mx-auto mb-3 shadow-md">
              <Heart className="w-6 h-6 text-pink-600" />
            </div>
            <h3 className="text-3xl font-extrabold text-gray-900 tracking-tight">{user.favoriteNeighborhoods?.length || 0}</h3>
            <p className="text-gray-600 font-medium">Saved</p>
          </div>
          <div className="card glassmorphism text-center shadow-glow card-hover transition-all duration-300">
            <div className="w-12 h-12 bg-gradient-to-br from-yellow-200 to-yellow-100 rounded-xl flex items-center justify-center mx-auto mb-3 shadow-md">
              <Star className="w-6 h-6 text-yellow-600" />
            </div>
            <h3 className="text-3xl font-extrabold text-gray-900 tracking-tight">{neighborhoods[0]?.matchScore || 0}%</h3>
            <p className="text-gray-600 font-medium">Best Match</p>
          </div>
          <div className="card glassmorphism text-center shadow-glow card-hover transition-all duration-300">
            <div className="w-12 h-12 bg-gradient-to-br from-purple-200 to-purple-100 rounded-xl flex items-center justify-center mx-auto mb-3 shadow-md">
              <MapPin className="w-6 h-6 text-purple-600" />
            </div>
            <h3 className="text-3xl font-extrabold text-gray-900 tracking-tight">{preferences?.city || 'India'}</h3>
            <p className="text-gray-600 font-medium">Search Area</p>
          </div>
        </div>

        {/* Preferences Summary */}
        {preferences && (
          <div className="card glassmorphism shadow-glow mb-8">
            <h2 className="text-2xl font-bold text-gray-900 mb-4 flex items-center gap-2">
              <Sparkles className="w-6 h-6 text-pink-500" /> Your Preferences
            </h2>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="flex flex-col items-center">
                <Briefcase className="w-5 h-5 text-indigo-500 mb-1" />
                <span className="text-xs text-gray-500">Work Style</span>
                <span className="chip mt-1">{preferences.workStyle || 'Not specified'}</span>
              </div>
              <div className="flex flex-col items-center">
                <Users className="w-5 h-5 text-pink-500 mb-1" />
                <span className="text-xs text-gray-500">Family Status</span>
                <span className="chip mt-1">{preferences.familyStatus || 'Not specified'}</span>
            </div>
              <div className="flex flex-col items-center">
                <Sparkles className="w-5 h-5 text-yellow-500 mb-1" />
                <span className="text-xs text-gray-500">Lifestyle</span>
                <span className="chip mt-1">{preferences.lifestyle || 'Not specified'}</span>
            </div>
              <div className="flex flex-col items-center">
                <Building2 className="w-5 h-5 text-purple-500 mb-1" />
                <span className="text-xs text-gray-500">City</span>
                <span className="chip mt-1">{preferences.city || 'Not specified'}</span>
            </div>
            </div>
          </div>
        )}

        {/* Neighborhood Matches */}
        <div className="mb-8">
          <h2 className="text-2xl font-extrabold text-gradient mb-6 bg-gradient-to-r from-indigo-700 via-purple-600 to-pink-500 bg-clip-text text-transparent drop-shadow-lg">Your Top Matches</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {neighborhoods.map((neighborhood) => (
              <div key={neighborhood._id} className="card glassmorphism card-hover shadow-glow transition-all duration-300 hover:scale-105">
                <div className="relative mb-4">
                  <img
                    src={neighborhood.imageUrl || 'https://images.unsplash.com/photo-1587474260584-136574528ed5?w=400'}
                    alt={neighborhood.name}
                    className="w-full h-48 object-cover rounded-lg shadow-md"
                  />
                  <div className="absolute top-3 right-3 bg-gradient-to-r from-indigo-600 to-pink-500 text-white px-3 py-1 rounded-full text-sm font-semibold shadow-glow">
                    {neighborhood.matchScore}% match
                  </div>
                </div>
                <h3 className="text-xl font-bold text-gray-900 mb-2 tracking-tight">
                  {neighborhood.name}
                </h3>
                <p className="text-lg text-indigo-700 font-semibold mb-3">{neighborhood.averageRent?.toLocaleString()}/month</p>
                <div className="flex flex-wrap gap-2 mb-4 justify-center">
                  {(neighborhood.tags || neighborhood.amenities?.slice(0, 3) || []).map((highlight, index) => (
                    <span
                      key={index}
                      className="chip bg-gradient-to-br from-gray-100 to-gray-200 text-gray-700"
                    >
                      {highlight}
                    </span>
                  ))}
                </div>
                <div className="flex space-x-3">
                  <button 
                    onClick={() => handleViewDetails(neighborhood._id)}
                    className="bg-gradient-to-r from-blue-500 to-purple-600 text-white font-semibold py-3 px-6 rounded-full hover:from-blue-600 hover:to-purple-700 transition-all duration-300 flex-1 shadow-lg hover:shadow-xl transform hover:scale-105"
                  >
                    View Details
                  </button>
                  <button 
                    onClick={() => handleGetDirections(neighborhood)}
                    className="bg-white text-gray-700 border-2 border-gray-200 hover:border-blue-300 hover:text-blue-600 px-4 py-3 rounded-full flex items-center justify-center shadow-md hover:shadow-lg transition-all duration-300"
                    title="Get Directions"
                  >
                    <Navigation className="w-4 h-4" />
                  </button>
                  <button 
                    onClick={() => handleToggleFavorite(neighborhood._id)}
                    className={`border-2 px-4 py-3 rounded-full flex items-center justify-center shadow-md hover:shadow-lg transition-all duration-300 ${user?.favoriteNeighborhoods?.includes(neighborhood._id) ? 'bg-red-50 text-red-600 border-red-300 hover:bg-red-100' : 'bg-white text-gray-700 border-gray-200 hover:border-pink-300 hover:text-pink-600 hover:bg-pink-50'}`}
                    title={user?.favoriteNeighborhoods?.includes(neighborhood._id) ? 'Remove from Favorites' : 'Add to Favorites'}
                  >
                    <Heart className={`w-4 h-4 ${user?.favoriteNeighborhoods?.includes(neighborhood._id) ? 'fill-current' : ''}`} />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* AI Recommendations */}
        <div className="mb-8">
          <AIRecommendationEngine />
        </div>

        {/* Quick Actions */}
        <div className="card glassmorphism shadow-glow">
          <h2 className="text-xl font-bold text-gray-900 mb-4 flex items-center gap-2">
            <Sparkles className="w-5 h-5 text-indigo-500" /> Quick Actions
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <button 
              onClick={() => navigate('/explore')} 
              className="bg-white text-gray-700 font-semibold py-4 px-6 rounded-full border-2 border-gray-200 hover:border-blue-300 hover:text-blue-600 hover:bg-blue-50 transition-all duration-300 shadow-md hover:shadow-lg transform hover:scale-105"
            >
              Explore All Neighborhoods
            </button>
            <button 
              onClick={() => navigate('/profile')} 
              className="bg-white text-gray-700 font-semibold py-4 px-6 rounded-full border-2 border-gray-200 hover:border-purple-300 hover:text-purple-600 hover:bg-purple-50 transition-all duration-300 shadow-md hover:shadow-lg transform hover:scale-105"
            >
              Update Preferences
            </button>
            <button 
              onClick={() => navigate('/onboarding')} 
              className="bg-white text-gray-700 font-semibold py-4 px-6 rounded-full border-2 border-gray-200 hover:border-green-300 hover:text-green-600 hover:bg-green-50 transition-all duration-300 shadow-md hover:shadow-lg transform hover:scale-105"
            >
              Retake Quiz
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard; 