import React, { useState } from 'react';
import { Star } from 'lucide-react';

const StarRating = ({ rating = 0, onRatingChange, readOnly = false }) => {
  const [hoverRating, setHoverRating] = useState(0);

  const handleClick = (rate) => {
    if (!readOnly && onRatingChange) {
      onRatingChange(rate);
    }
  };

  const handleMouseEnter = (rate) => {
    if (!readOnly) {
      setHoverRating(rate);
    }
  };

  const handleMouseLeave = () => {
    if (!readOnly) {
      setHoverRating(0);
    }
  };

  return (
    <div className="flex items-center">
      {[...Array(5)].map((_, index) => {
        const rate = index + 1;
        return (
          <Star
            key={rate}
            onClick={() => handleClick(rate)}
            onMouseEnter={() => handleMouseEnter(rate)}
            onMouseLeave={handleMouseLeave}
            className={`
              w-6 h-6 cursor-${readOnly ? 'default' : 'pointer'}
              transition-colors duration-200
              ${rate <= (hoverRating || rating) ? 'text-yellow-400 fill-current' : 'text-gray-300'}
            `}
          />
        );
      })}
    </div>
  );
};

export default StarRating; 