import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { neighborhoodAPI, userAPI } from '../services/api';
import { Search, Filter, MapPin, Heart, Star, Navigation } from 'lucide-react';
import { useToast } from '../context/ToastContext';
import { useUser } from '../context/UserContext';
import VanillaTilt from 'vanilla-tilt';
import AISearchEnhancement from '../components/ai/AISearchEnhancement';
import StarRating from '../components/common/StarRating';

function Explore() {
  const [neighborhoods, setNeighborhoods] = useState([]);
  const [filteredNeighborhoods, setFilteredNeighborhoods] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [filters, setFilters] = useState({
    city: '',
    minRent: 10000,
    maxRent: 100000,
    minSafetyScore: 0,
    amenities: []
  });
  const [showFilters, setShowFilters] = useState(false);
  const navigate = useNavigate();
  const { showSuccess, showError } = useToast();
  const { user, setUser } = useUser();

  const cities = ['Mumbai', 'Delhi', 'Bangalore', 'Hyderabad', 'Chennai', 'Pune', 'Kolkata'];
  const amenityOptions = [
    'Metro Access', 'IT Parks', 'Shopping Malls', 'Restaurants', 'Gyms', 
    'Schools', 'Hospitals', 'Parks', 'Temples', 'Markets', 'Street Food',
    'Banks/ATMs', 'Pharmacies', 'Bus Stops', 'Auto Stands', 'Vegetarian Restaurants',
    'International Schools', 'Coaching Centers', 'Community Centers', 'Gardens',
    'Cinema Halls', 'Libraries', 'Sports Complex', 'Medical Stores'
  ];

  const fetchNeighborhoods = useCallback(async () => {
    try {
      setLoading(true);
      const response = await neighborhoodAPI.getAll();
      setNeighborhoods(response.data);
    } catch (error) {
      console.error('Error fetching neighborhoods:', error);
      showError('Failed to fetch neighborhoods. Please try again.');
    } finally {
      setLoading(false);
    }
  }, [showError]);

  const filterNeighborhoods = useCallback(() => {
    let filtered = neighborhoods;

    if (searchTerm) {
      filtered = filtered.filter(n => 
        n.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        n.city.toLowerCase().includes(searchTerm.toLowerCase()) ||
        n.description.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (filters.city) {
      filtered = filtered.filter(n => n.city === filters.city);
    }

    filtered = filtered.filter(n => 
      n.averageRent >= filters.minRent && n.averageRent <= filters.maxRent
    );

    if (filters.minSafetyScore > 0) {
      filtered = filtered.filter(n => n.safetyScore >= filters.minSafetyScore);
    }

    if (filters.amenities.length > 0) {
      filtered = filtered.filter(n => 
        filters.amenities.some(amenity => n.amenities.includes(amenity))
      );
    }

    setFilteredNeighborhoods(filtered);
  }, [neighborhoods, searchTerm, filters]);

  useEffect(() => {
    fetchNeighborhoods();
  }, [fetchNeighborhoods]);

  useEffect(() => {
    filterNeighborhoods();
  }, [filterNeighborhoods]);

  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const handleAmenityToggle = (amenity) => {
    setFilters(prev => ({
      ...prev,
      amenities: prev.amenities.includes(amenity)
        ? prev.amenities.filter(a => a !== amenity)
        : [...prev.amenities, amenity]
    }));
  };

  const clearFilters = () => {
    setFilters({
      city: '',
      minRent: 10000,
      maxRent: 100000,
      minSafetyScore: 0,
      amenities: []
    });
    setSearchTerm('');
  };

  const handleViewDetails = (neighborhoodId) => {
    navigate(`/neighborhoods/${neighborhoodId}`);
  };

  const handleToggleFavorite = async (neighborhoodId) => {
    try {
      if (!user) {
        showError('Please log in to save favorites.');
        return;
      }

      const isCurrentlyFavorite = user.favoriteNeighborhoods?.includes(neighborhoodId);
      
      let response;
      if (isCurrentlyFavorite) {
        response = await userAPI.removeFavorite(neighborhoodId);
        showSuccess('Neighborhood removed from favorites!');
      } else {
        response = await userAPI.addFavorite(neighborhoodId);
        showSuccess('Neighborhood added to favorites!');
      }
      
      if (response && response.data) {
        setUser({
          ...user,
          favoriteNeighborhoods: response.data.favoriteNeighborhoods || []
        });
      }
    } catch (error) {
      showError('Failed to update favorites. Please try again.');
    }
  };

  const handleGetDirections = (neighborhood) => {
    if (!neighborhood || !neighborhood.location || !neighborhood.location.coordinates) {
      showError('Location coordinates not available for this neighborhood.');
      return;
    }

    const [longitude, latitude] = neighborhood.location.coordinates;
    const destination = `${latitude},${longitude}`;

    const isMobile = /Android|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

    let mapUrl;
    
    if (isMobile) {
      if (/iPhone|iPad|iPod/i.test(navigator.userAgent)) {
        mapUrl = `maps://maps.apple.com/?daddr=${destination}&dirflg=d&t=m`;
        
        const appleMapsLink = document.createElement('a');
        appleMapsLink.href = mapUrl;
        appleMapsLink.click();
        
        setTimeout(() => {
          window.open(`https://maps.google.com/maps?daddr=${destination}&dirflg=d`, '_blank');
        }, 500);
        return;
      } else if (/Android/i.test(navigator.userAgent)) {
        mapUrl = `google.navigation:q=${destination}`;
        
        const googleMapsLink = document.createElement('a');
        googleMapsLink.href = mapUrl;
        googleMapsLink.click();
        
        setTimeout(() => {
          window.open(`https://maps.google.com/maps?daddr=${destination}&dirflg=d`, '_blank');
        }, 500);
        return;
      }
    }

    mapUrl = `https://maps.google.com/maps?daddr=${destination}&dirflg=d`;
    window.open(mapUrl, '_blank');
    
    showSuccess('Opening directions in map application...');
  };

  function TiltCard({ children, options, ...rest }) {
    const tiltRef = useRef(null);
    useEffect(() => {
      if (tiltRef.current) {
        VanillaTilt.init(tiltRef.current, options || {
          max: 15,
          speed: 400,
          glare: true,
          'max-glare': 0.18,
          scale: 1.04,
        });
      }
      return () => {
        const currentTiltRef = tiltRef.current;
        if (currentTiltRef && currentTiltRef.vanillaTilt) {
          currentTiltRef.vanillaTilt.destroy();
        }
      };
    }, [options]);
    return (
      <div ref={tiltRef} {...rest}>
        {children}
      </div>
    );
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading neighborhoods...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 sm:py-8">
        <div className="mb-6 sm:mb-8">
          <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold text-gray-900 mb-2">
            Explore Neighborhoods
          </h1>
          <p className="text-sm sm:text-base text-gray-600">
            Discover your perfect neighborhood across Indian cities
          </p>
        </div>

        <div className="bg-white rounded-lg shadow-sm p-4 sm:p-6 mb-6 sm:mb-8">
          <div className="flex flex-col gap-4">
            <div className="flex-1">
              <input
                type="text"
                value={searchTerm}
                onChange={e => setSearchTerm(e.target.value)}
                placeholder="Search neighborhoods..."
                className="w-full p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>

            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 sm:gap-0">
              <button
                onClick={() => setShowFilters(!showFilters)}
                className="bg-white text-gray-700 font-semibold py-2 sm:py-3 px-4 sm:px-6 rounded-full border-2 border-gray-200 hover:border-blue-300 hover:text-blue-600 transition-all duration-300 shadow-md hover:shadow-lg flex items-center text-sm sm:text-base"
              >
                <Filter className="w-4 h-4 mr-2" />
                <span className="hidden sm:inline">Advanced Filters</span>
                <span className="sm:hidden">Filters</span>
                {(filters.city || filters.amenities.length > 0 || filters.minSafetyScore > 0) && (
                  <span className="ml-2 bg-blue-600 text-white rounded-full w-5 h-5 text-xs flex items-center justify-center">
                    {[filters.city, ...filters.amenities, filters.minSafetyScore > 0 ? 'safety' : ''].filter(Boolean).length}
                  </span>
                )}
              </button>
              
              <button
                onClick={() => {
                  setSearchTerm('');
                  clearFilters();
                  filterNeighborhoods();
                }}
                className="text-sm text-gray-600 hover:text-gray-800 transition-colors"
              >
                Reset All
              </button>
            </div>
          </div>

          {showFilters && (
            <div className="mt-6 pt-6 border-t border-gray-200">
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 sm:gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">City</label>
                  <select
                    value={filters.city}
                    onChange={(e) => handleFilterChange('city', e.target.value)}
                    className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500"
                  >
                    <option value="">All Cities</option>
                    {cities.map(city => (
                      <option key={city} value={city}>{city}</option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Max Rent: {filters.maxRent.toLocaleString('en-IN')}
                  </label>
                  <input
                    type="range"
                    min="10000"
                    max="150000"
                    step="5000"
                    value={filters.maxRent}
                    onChange={(e) => handleFilterChange('maxRent', parseInt(e.target.value))}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Min Safety Score: {filters.minSafetyScore}/10
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="10"
                    step="0.5"
                    value={filters.minSafetyScore}
                    onChange={(e) => handleFilterChange('minSafetyScore', parseFloat(e.target.value))}
                    className="w-full"
                  />
                </div>

                <div className="flex items-end">
                  <button
                    onClick={clearFilters}
                    className="bg-white text-gray-700 font-semibold py-3 px-6 rounded-full border-2 border-gray-200 hover:border-red-300 hover:text-red-600 hover:bg-red-50 transition-all duration-300 shadow-md hover:shadow-lg w-full"
                  >
                    Clear Filters
                  </button>
                </div>
              </div>

              <div className="mt-6">
                <label className="block text-sm font-medium text-gray-700 mb-3">Amenities</label>
                <div className="flex flex-wrap gap-1 sm:gap-2">
                  {amenityOptions.map(amenity => (
                    <button
                      key={amenity}
                      onClick={() => handleAmenityToggle(amenity)}
                      className={`px-2 sm:px-4 py-1 sm:py-2 rounded-full text-xs sm:text-sm border-2 transition-all duration-300 shadow-sm hover:shadow-md ${
                        filters.amenities.includes(amenity)
                          ? 'bg-blue-600 text-white border-blue-600 shadow-md'
                          : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:text-blue-600'
                      }`}
                    >
                      {amenity}
                    </button>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="mb-4 sm:mb-6">
          <p className="text-sm sm:text-base text-gray-600">
            Showing {filteredNeighborhoods.length} of {neighborhoods.length} neighborhoods
          </p>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
          {filteredNeighborhoods.map((neighborhood) => (
            <TiltCard key={neighborhood._id} className="card card-hover" options={{max:15,speed:400,glare:true,'max-glare':0.18,scale:1.04}}>
              <div className="relative mb-4">
                <img
                  src={neighborhood.imageUrl || 'https://images.unsplash.com/photo-1587474260584-136574528ed5?w=400'}
                  alt={neighborhood.name}
                  className="w-full h-48 object-cover rounded-lg"
                />
                <button
                  onClick={() => handleToggleFavorite(neighborhood._id)}
                  className={`absolute top-3 right-3 p-3 rounded-full shadow-lg transition-all duration-300 transform hover:scale-110 ${
                    user?.favoriteNeighborhoods?.includes(neighborhood._id)
                      ? 'bg-red-500 hover:bg-red-600 shadow-red-200'
                      : 'bg-white hover:bg-gray-50 shadow-gray-200'
                  }`}
                  title={user?.favoriteNeighborhoods?.includes(neighborhood._id) ? 'Remove from Favorites' : 'Add to Favorites'}
                >
                  <Heart className={`w-4 h-4 ${
                    user?.favoriteNeighborhoods?.includes(neighborhood._id)
                      ? 'text-white fill-current'
                      : 'text-gray-600'
                  }`} />
                </button>
              </div>

              <div className="p-4 sm:p-6">
                <div className="flex items-center justify-between">
                  <h3 className="text-xl sm:text-2xl font-bold text-gray-900 truncate">
                    {neighborhood.name}
                  </h3>
                  <p className="text-lg sm:text-xl font-semibold text-primary-600">
                    {neighborhood.averageRent.toLocaleString('en-IN')}/mo
                  </p>
                </div>
                <div className="flex items-center justify-between mt-1">
                  <p className="text-sm text-gray-600 flex items-center">
                    <MapPin className="w-4 h-4 mr-1 text-gray-500" />
                    {neighborhood.city}
                  </p>
                  {neighborhood.numRatings > 0 ? (
                    <div className="flex items-center">
                      <StarRating rating={neighborhood.averageRating} readOnly={true} />
                      <span className="ml-2 text-xs text-gray-500">({neighborhood.numRatings})</span>
                    </div>
                  ) : (
                    <span className="text-xs text-gray-400">Not yet rated</span>
                  )}
                </div>
                <p className="text-gray-700 mt-3 h-12 overflow-hidden text-ellipsis">
                  {neighborhood.description}
                </p>

                <div className="flex items-center justify-between mb-4">
                  <span className="text-lg font-bold text-primary-600">
                    {neighborhood.averageRent?.toLocaleString()}/month
                  </span>
                  <span className="flex items-center text-yellow-600 font-semibold">
                    <Star className="w-4 h-4 mr-1" />
                    {neighborhood.safetyScore}/10
                  </span>
                </div>

                <div className="flex flex-wrap gap-2 mb-4">
                  {(neighborhood.tags || neighborhood.amenities?.slice(0, 3) || []).map((highlight, index) => (
                    <span
                      key={index}
                      className="chip bg-gray-100 text-gray-700"
                    >
                      {highlight}
                      </span>
                    ))}
                </div>

                <div className="flex space-x-3">
                  <button
                    onClick={() => handleViewDetails(neighborhood._id)}
                    className="bg-gradient-to-r from-blue-500 to-purple-600 text-white font-semibold py-3 px-6 rounded-full hover:from-blue-600 hover:to-purple-700 transition-all duration-300 flex-1 shadow-lg hover:shadow-xl transform hover:scale-105"
                  >
                    View Details
                  </button>
                  <button
                    onClick={() => handleGetDirections(neighborhood)}
                    className="bg-white text-gray-700 border-2 border-gray-200 hover:border-blue-300 hover:text-blue-600 px-4 py-3 rounded-full flex items-center justify-center shadow-md hover:shadow-lg transition-all duration-300"
                    title="Get Directions"
                  >
                    <Navigation className="w-4 h-4" />
                  </button>
                </div>
              </div>
            </TiltCard>
          ))}
        </div>

        {filteredNeighborhoods.length === 0 && (
          <div className="text-center py-12">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <Search className="w-8 h-8 text-gray-400" />
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">No neighborhoods found</h3>
            <p className="text-gray-600 mb-4">
              Try adjusting your search criteria or filters to find more results.
            </p>
            <button
              onClick={clearFilters}
              className="bg-gradient-to-r from-blue-500 to-purple-600 text-white font-semibold py-3 px-8 rounded-full hover:from-blue-600 hover:to-purple-700 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105"
            >
              Clear All Filters
            </button>
          </div>
        )}
      </div>
      <style>{`
        .card-hover {
          transition: transform 0.3s ease, box-shadow 0.3s ease;
        }
        .card-hover:hover {
          transform: translateY(-10px);
          box-shadow: 0 20px 25px -5px rgba(0,0,0,0.1), 0 10px 10px -5px rgba(0,0,0,0.04);
        }
        .chip {
          padding: 0.25rem 0.75rem;
          border-radius: 9999px;
          font-size: 0.75rem;
          font-weight: 500;
        }
      `}</style>
    </div>
  );
}

export default Explore;