import React, { useState, useRef, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useUser } from '../../context/UserContext';
import { useToast } from '../../context/ToastContext';
import { Home, Map, User, Menu, X, Heart, Settings } from 'lucide-react';

function Navbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);
  const navRef = useRef(null);
  const { user, setUser } = useUser();
  const { showSuccess } = useToast();
  const location = useLocation();
  const navigate = useNavigate();

  const isActive = (path) => location.pathname === path;

  const handleLogout = () => {
    setUser(null);
    localStorage.removeItem('neighborfit_user');
    localStorage.removeItem('token');
    showSuccess('You have been logged out successfully. See you soon! ');
    navigate('/');
    setIsUserMenuOpen(false);
  };

  const navItems = [
    { path: '/', label: 'Home', icon: Home },
    { path: '/explore', label: 'Explore', icon: Map },
    { path: '/dashboard', label: 'Dashboard', icon: Heart },
  ];

  useEffect(() => {
    const updateMarker = () => {
      if (!navRef.current) return;
      
      const activeItem = navRef.current.querySelector('.nav-item.active');
      if (activeItem) {
      }
    };

    updateMarker();
    window.addEventListener('resize', updateMarker);
    
    return () => window.removeEventListener('resize', updateMarker);
  }, [location.pathname]);

  return (
    <>
      <nav
        className="fixed top-4 sm:top-6 left-1/2 transform -translate-x-1/2 z-50 w-full flex justify-center px-2 sm:px-4"
        aria-label="Main navigation"
      >
        <div
          className="bg-white/90 backdrop-blur-xl shadow-xl border border-gray-200 rounded-full flex items-center max-w-6xl w-full px-4 sm:px-8 lg:px-12 py-2 sm:py-3"
        >
          <div className="flex items-center gap-x-2 sm:gap-x-4 pr-4 sm:pr-8 border-r border-gray-200">
            <Link to="/" className="flex items-center space-x-2 sm:space-x-3 px-1 sm:px-2 py-1">
              <div className="w-6 h-6 sm:w-8 sm:h-8 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                <Home className="w-3 h-3 sm:w-4 sm:h-4 text-white" />
              </div>
              <span className="text-lg sm:text-xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent hidden sm:block">
                NeighborFit
              </span>
            </Link>
          </div>

          <div className="hidden md:flex items-center gap-x-6 lg:gap-x-10 pl-4 sm:pl-8" ref={navRef}>
            {navItems.map(({ path, label, icon: Icon }) => {
              const active = isActive(path);
              return (
              <Link
                key={path}
                to={path}
                  className={`group nav-item flex items-center space-x-1 sm:space-x-2 px-4 sm:px-6 lg:px-8 py-2 sm:py-4 rounded-full transition-all duration-300 relative z-10 focus:outline-none ${
                    active
                      ? 'active text-blue-700 font-semibold'
                      : 'text-gray-600 hover:text-blue-600'
                  }`}
                  aria-current={active ? 'page' : undefined}
                >
                  {active && (
                    <span
                      className="absolute inset-0 bg-gradient-to-r from-blue-100 to-purple-100 rounded-full shadow-md border border-blue-200/40 transition-all duration-300 ease-out z-0"
                      aria-hidden="true"
                    />
                  )}
                  <span
                    className="absolute inset-0 rounded-full transition-all duration-300 ease-out z-10 opacity-0 group-hover:opacity-100 group-focus:opacity-100 bg-gradient-to-r from-blue-50 to-purple-50 pointer-events-none"
                    aria-hidden="true"
                  />
                  <Icon className="w-3 h-3 sm:w-4 sm:h-4 relative z-20" />
                  <span className="font-medium text-sm sm:text-base relative z-20">{label}</span>
              </Link>
              );
            })}
          </div>

          <div className="hidden sm:flex items-center gap-x-4 lg:gap-x-6 ml-auto">
            {user ? (
              <div className="relative">
                <button
                  onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                  className="flex items-center space-x-3 px-5 py-3 rounded-full hover:bg-gray-100/50 transition-all duration-200 border border-transparent hover:border-gray-200/50"
                >
                  <div className="w-7 h-7 sm:w-8 sm:h-8 bg-gradient-to-r from-green-400 to-blue-500 rounded-full flex items-center justify-center">
                    <User className="w-3 h-3 sm:w-4 sm:h-4 text-white" />
                  </div>
                  <span className="text-sm sm:text-base font-medium text-gray-700 hidden lg:block">
                    {user.name || 'User'}
                  </span>
                </button>
                {isUserMenuOpen && (
                  <div className="absolute right-0 mt-3 w-48 bg-white/95 backdrop-blur-xl rounded-2xl shadow-lg border border-white/20 py-2 z-50">
                    <Link
                      to="/profile"
                      className="flex items-center space-x-3 px-4 py-3 text-sm text-gray-700 hover:bg-gray-100/50 rounded-xl mx-2 transition-colors"
                      onClick={() => setIsUserMenuOpen(false)}
                    >
                      <Settings className="w-4 h-4" />
                      <span>Settings</span>
                    </Link>
                    <button
                      onClick={handleLogout}
                      className="flex items-center space-x-3 w-full px-4 py-3 text-sm text-red-600 hover:bg-red-50/50 rounded-xl mx-2 transition-colors"
                    >
                      <X className="w-4 h-4" />
                      <span>Logout</span>
                    </button>
                  </div>
                )}
              </div>
            ) : (
              <div className="flex items-center space-x-2 sm:space-x-3">
                <Link
                  to="/onboarding"
                  className="px-4 sm:px-6 py-2 sm:py-3 text-sm sm:text-base font-medium text-white bg-gradient-to-r from-blue-500 to-purple-600 rounded-full hover:from-blue-600 hover:to-purple-700 transition-all duration-200 shadow-lg hover:shadow-xl"
                >
                  Sign Up
                </Link>
              </div>
            )}
          </div>

          <div className="sm:hidden flex items-center">
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="p-3 rounded-full text-gray-600 hover:text-gray-900 hover:bg-gray-100/50 transition-colors"
            >
              {isMenuOpen ? (
                <X className="w-6 h-6" />
              ) : (
                <Menu className="w-6 h-6" />
              )}
            </button>
          </div>
        </div>
      </nav>

        {isMenuOpen && (
        <>
          <div
            className="fixed inset-0 bg-black bg-opacity-30 z-40 transition-opacity duration-300"
            onClick={() => setIsMenuOpen(false)}
            aria-label="Close menu overlay"
          />
          <div
            className="fixed top-0 right-0 w-4/5 max-w-xs h-full bg-white/95 backdrop-blur-xl rounded-l-2xl shadow-2xl border-l border-white/20 py-6 px-4 z-50 transform translate-x-0 transition-transform duration-300 animate-slide-in"
            style={{ animation: 'slideInRight 0.3s cubic-bezier(0.4,0,0.2,1)' }}
            role="dialog"
            aria-modal="true"
          >
            <button
              className="absolute top-4 right-4 p-2 rounded-full text-gray-600 hover:text-gray-900 hover:bg-gray-100/50 transition-colors"
              onClick={() => setIsMenuOpen(false)}
              aria-label="Close menu"
            >
              <X className="w-6 h-6" />
            </button>
            <div className="space-y-1 mt-10">
              {navItems.map(({ path, label, icon: Icon }) => (
                <Link
                  key={path}
                  to={path}
                  className={`flex items-center space-x-3 px-4 py-3 rounded-xl transition-all duration-200 text-lg font-medium ${
                    isActive(path)
                      ? 'bg-gradient-to-r from-blue-500/20 to-purple-500/20 text-blue-700 border border-blue-300/30'
                      : 'text-gray-600 hover:text-blue-600 hover:bg-blue-50/50'
                  }`}
                  onClick={() => setIsMenuOpen(false)}
                >
                  <Icon className="w-5 h-5" />
                  <span>{label}</span>
                </Link>
              ))}
              {user ? (
                <div className="pt-3 border-t border-gray-200/50 mt-3">
                  <Link
                    to="/profile"
                    className="flex items-center space-x-3 px-4 py-3 text-gray-600 hover:text-blue-600 hover:bg-blue-50/50 rounded-xl transition-colors"
                    onClick={() => setIsMenuOpen(false)}
                  >
                    <Settings className="w-5 h-5" />
                    <span>Settings</span>
                  </Link>
                  <button
                    onClick={handleLogout}
                    className="flex items-center space-x-3 w-full px-4 py-3 text-red-600 hover:bg-red-50/50 rounded-xl mt-1 transition-colors"
                  >
                    <X className="w-5 h-5" />
                    <span>Logout</span>
                  </button>
                </div>
              ) : (
                <div className="pt-3 border-t border-gray-200/50 mt-3 flex flex-col space-y-2">
                  <Link
                    to="/signin"
                    className="px-4 py-3 text-center font-medium text-gray-600 hover:text-blue-600 rounded-xl hover:bg-blue-50/50 transition-colors"
                    onClick={() => setIsMenuOpen(false)}
                  >
                    Sign In
                  </Link>
                  <Link
                    to="/onboarding"
                    className="px-4 py-3 text-center font-medium text-white bg-gradient-to-r from-blue-500 to-purple-600 rounded-xl hover:from-blue-600 hover:to-purple-700 transition-all duration-200 shadow-lg"
                    onClick={() => setIsMenuOpen(false)}
                  >
                    Sign Up
                  </Link>
                </div>
              )}
            </div>
          </div>
        </>
      )}
    </>
  );
}

export default Navbar; 